.page404 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.page404__title, .page404__description {
  background: var(--red-color);
  background: -webkit-linear-gradient(to right, var(--red-color) 18%, var(--pink-color) 56%, var(--yellow-color) 100%);
  background: -moz-linear-gradient(to right, var(--red-color) 18%, var(--pink-color) 56%, var(--yellow-color) 100%);
  background: linear-gradient(to right, var(--red-color) 18%, var(--pink-color) 56%, var(--yellow-color) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.page404__title {
  font-size: 150px;
  line-height: 1;
}

.page404__description {
  font-size: 30px;
  margin-bottom: 30px;
}


